#info{
    width: 100%;
    height: 100vh;
    background: #fff;
    padding: 150px 0px;

    .inner{
        width: 100%;
        margin: 0 auto;

        h1{
            font:bold 22px/1 'arial';
            color:#333;
            text-align: center;
            margin-bottom: 50px;
        }

        .wrap{
            width: 100%;
            height: 700px;
            padding:100px 0px;

            .swiper{
                height: 100%;
                
                .swiper-slide{
                    background: hotpink;
                    height: 100%;
                    width: 500px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color:#fff;
                }
            }
        }
    }
}
