.community{

    figure{
        width: 100%;
        height: 100vh;
        position: relative; 
        margin-top: -100px;

        // video{
        //     width:100%;
        //     height:100%;
        //     object-fit: cover;
        // }

        img{
            width:100%;
            height:100%;
            object-fit: cover;
        }
        
        h1{
            position: absolute;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:bold 30px/1 'Orbitron';
            color:#fff;
            letter-spacing: 2.3px;
        }

        p{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:normal 18px/1 'Orbitron';
            color:#ddd;
            letter-spacing: 1.3px;
        }

        span{
            position: absolute;
            top: 55%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:normal 14px/1 'Orbitron';
            color:#ddd;
            letter-spacing: 1.3px;
        }

        b{
            position: absolute;
            top: 95%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:bold 14px/1 'Orbitron';
            color:#eee;
            letter-spacing: 1.7px;
        }
    }
    
    .inner{
        >section{
            .inputBox{
                width: 100%;
                padding: 50px;
                margin-bottom: 50px;
                border:1px solid #aaa;
            }

            .showList{
                width: 100%;
                padding: 50px;
                border:1px solid #aaa;
                
                article{
                    width: 100%;
                    padding:30px;
                    border:1px solid #bbb;
                    margin-bottom: 20px;
                }
            }
        }
    }
}