.location{

    figure{
        width: 100%;
        height: 75vh;
        position: relative; 
        margin-top: -100px;

        video{
            width:100%;
            height:100%;
            object-fit: cover;
        }
        
        h1{
            position: absolute;
            top: 40%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:bold 30px/1 'Orbitron';
            color:#fff;
            letter-spacing: 2.3px;
        }

        p{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:normal 18px/1 'Orbitron';
            color:#ddd;
            letter-spacing: 1.3px;
        }

        span{
            position: absolute;
            top: 60%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:normal 14px/1 'Orbitron';
            color:#ddd;
            letter-spacing: 1.3px;
        }
    }
    
    .inner{
        position:relative;
        width: 100%;

        >section{
            #map{
                width: 100%;
                height: 500px;
                background: #000;
                filter:grayscale(100%);

                img{
                    object-fit: contain;
                }
            }

            .branch{
                width: 1180px;
                position:absolute;
                top:-50px;
                left:50%;
                transform: translateX(-50%);
                display: flex;
                justify-content: space-between;
                align-items: center;

                button{
                    width: 25%;
                    height: 100px;
                    border:none;
                    background: #fff;
                    box-shadow: 5px 5px 10px rgba(0,0,0,.5);
                    font:bold 12px/1 'Orbitron';
                    color:#f9bf7c;
                    letter-spacing: 1.5px;
                    transition:all 1s;
                    cursor: pointer;
                    position: relative;

                    &.on{

                    }

                    &:hover{
                        transform: translateY(-25px);

                        &::after{
                            content: '';
                            display: block;
                            height: 5px;
                            position: absolute;
                            background: #f9bf7c;
                            top: -15px;
                            right: 50%;
                            animation: locationAni 1s;
                            animation-fill-mode: forwards;
                        }

                        &::before{
                            content: '';
                            display: block;
                            height: 5px;
                            position: absolute;
                            background: #f9bf7c;
                            top: -15px;
                            left:50%;
                            animation: locationAni 1s;
                            animation-fill-mode: forwards;
                        }
                    }

                    p{
                        font:normal 10px/1 'Orbitron';
                        color:#777;
                        letter-spacing: 1px;
                        margin: 15px 0;
                    }

                    span{
                        font:bold 10px/1 'Orbitron';
                        color:#333;
                        letter-spacing: 1.8px;
                    }
                    
                }
            }
        }

        .contact{
            width: 1180px;
            margin: 0 auto;
            position: relative;
            padding:50px 0px 100px;

            h2{
                font:bold 25px/1 'Orbitron';
                color:#111;
                letter-spacing: 2.3px;
                text-align: center;
                margin-bottom:30px;
            }

            p{
                font:normal 10px/13px 'Orbitron';
                color:#777;
                letter-spacing: 1.3px;
                text-align: center;
                width: 60%;
                margin: 0 auto;
                margin-bottom: 50px;
            }

            form{
                width: 100%;

                table{
          
                    tbody{
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        flex-wrap: wrap;

                        tr{
                            width: 50%;
                            padding:10px 30px;

                            &:nth-child(5){
                                width: 100%;

                                td{
                                    width:1200px;
                                    padding:10px 15px;

                                    textarea{
                                        width: 100%;
                                        height: 100%;
                                        padding:10px 15px;
                                    }
                                }
                            }

                            td{
                                width:600px;
                                height:50px;
                                padding:10px 15px;
                                border:none;
          
                                input{
                                    width: 100%;
                                    height: 100%;
                                    padding:10px 15px;
                                }
                            }

                            .btnSet{
                                width: 150px;
                                height:30px;
                                position:absolute;
                                right:0px;
                                margin-right:45px;
                                
                                input{
                                    width:100%;
                                    height:100%;
                                    // background: #b7f4fcf6;
                                    background:#1e2831;
                                    border:none;
                                    font:bold 11px/13px 'Orbitron';
                                    color:#fff;
                                    letter-spacing: 1.3px;
                                    cursor:pointer;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@keyframes locationAni{
    0%{width: 0%;}
    100%{width: 40%;}
}