@import url('https://fonts.googleapis.com/css2?family=Orbitron&family=Playfair+Display:ital@0;1&display=swap');

.gallery{
    background: #efefef;

    figure{
        width: 100%;
        height: 100vh;
        position: relative; 
        margin-top: -100px;

        video{
            width:100%;
            height:100%;
            object-fit: cover;
        }
        
        h1{
            position: absolute;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:bold 30px/1 'Orbitron';
            color:#fff;
            letter-spacing: 2.3px;
        }

        p{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:normal 18px/1 'Orbitron';
            color:#ddd;
            letter-spacing: 1.3px;
        }

        span{
            position: absolute;
            top: 55%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:normal 14px/1 'Orbitron';
            color:#ddd;
            letter-spacing: 1.3px;
        }

        b{
            position: absolute;
            top: 95%;
            left: 50%;
            transform: translate(-50%, -50%);
            font:bold 14px/1 'Orbitron';
            color:#eee;
            letter-spacing: 1.7px;
        }
    }


    .inner{

        .gallerySlider{
            position: relative;
            margin: 0 auto;
            width: 1180px;
            height: 800px;
            overflow: hidden;
            border:1px solid red;
            margin-bottom: 50px;

            ul{
                position: absolute;
                top: 0;
                left: 0;
                width: calc(100% * 5);
                height: 100%;

                li{
                    width: calc(100% / 5);
                    height: 100%;
                    float: left;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    padding:50px 100px;
                    border:1px solid blue;

                    img{
                        width: 50%;
                        height: 100%;
                        object-fit: cover;
                    }
                    
                    .contents{
                        width: 50%;
                        height: 100%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        flex-wrap: wrap;
                        border:1px solid black;
                        text-align: center;

                        h3{
                            font:bold 16px/1 'Orbitron';
                            color:#111;
                            letter-spacing: 2px;
                            margin-bottom: 30px;
                            width: 80%;
                        }

                        p{
                            font:normal 11px/15px 'Orbitron';
                            color:#333;
                            letter-spacing: 1.3px;
                            margin-bottom: 15px;
                            width: 80%;
                        }
                        span{
                            font:bold 10px/1 'Orbitron';
                            color:#333;
                            letter-spacing: 1.1px;
                            margin-bottom: 30px;
                        }

                        .pages{
                            display: flex;
                            flex-direction: column;
                            width: 20%;
                            
                            b{
                                font:bold 10px/1 'Orbitron';
                                color:#333;
                                letter-spacing: 1.1px;


                                &:nth-child(1){
                                    margin-bottom: 10px;
                                    text-align: left;
                                }

                                &:nth-child(3){
                                    text-align: right;
                                }
                            }

                            >div{
                                width: 100%;
                                height: 2px;
                                background: #000;
                                margin-bottom: 10px;
                            }
                        }
                    }
                }
            }

            .prev{
                position: absolute;
                top: 50%;
                left: 45px;
                transform: translateY(-50%);
                font:bold 15px/1 'Orbitron';
                color:#333;
                cursor: pointer;
                border:none;
                background: none;
            }

            .next{
                position: absolute;
                top: 50%;
                right: 45px;
                transform: translateY(-50%);
                font:bold 15px/1 'Orbitron';
                color:#333;
                cursor: pointer;
                border:none;
                background: none;
            }
        }

        .galleryList{
            &::after{
                content:'';
                display: block;
                clear: both;
            }

            article{
                width: 20%;
                height: 300px;
                float: left;
                padding:10px;

                >.inner{
                    width: 100%;
                    height: 100%;
                    background: #fff;
                    box-shadow:5px 5px 10px rgba(0,0,0,.1);
                    padding: 10px;

                    .pic{
                        width: 100%;
                        height: 180px;
                        margin-bottom: 20px;
                        cursor: pointer;

                        img{
                            object-fit: cover;
                            width: 100%;
                            height: 100%;
                        }
                    }

                    h2{
                        font:normal 14px/1.2 'arial';
                        color:#777;
                    }
                }
            }
        }
    }
}

.popup{
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.9);
    position: fixed;
    top: 0;
    left: 0;
    padding:5vw;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    span{
        font:14px/1 'arial';
        color:#fff;
        position: absolute;
        top: 4vh;
        right: 5vw;
        cursor: pointer;
    }
}